<template>

    <div class="c-rkw-bookmark-modal" :class="{'c-rkw-bookmark-modal--open': open === true}">
        <div class="c-rkw-bookmark-modal__wrapper">
            <div class="o-container">
                <a href="#" class="logo"></a>
                <a href="#" class="c-rkw-bookmark-modal__close" @click.prevent="toggleBookmarkModal"></a>
                <header class="c-rkw-bookmark-modal__header">
                    <h2 class="c-rkw-bookmark-modal__headline">Ihre Merkliste</h2>
                    <a class="c-rkw-bookmark-modal__function--email" href="#"></a>
                    <a class="c-rkw-bookmark-modal__function--pdf" href="#"></a>
                </header>
                <div class="o-grid">
                    <transition name="fade">
                        <div v-if="!entries.length" class="o-grid__col u-12/12">
                            <p class="c-rkw-bookmark-modal__empty-info">
                                Leider haben Sie noch keine Daten in Ihrer Merkliste!
                            </p>
                        </div>
                    </transition>
                    <div v-for="entry in entries" class="o-grid__col u-3/12">
                        <transition name="fade" v-on:after-leave="afterLeave">
                            <div v-if="entry !== removing" :class="`c-rkw-bookmark-modal__bookmark c-rkw-bookmark-modal__bookmark${getContentColor(entry)}`" :style="`background-image: url(${settings.pageUrl}${getBookmarkContent(entry).sliderImages[0][1]});`" @click.prevent="openContentModal(entry)">
                                <span class="c-rkw-bookmark-modal__bookmark-remove" v-on:click.stop="removeQueue(entry)"></span>
                                <span class="c-rkw-bookmark-modal__bookmark-text">
                                    {{getBookmarkContent(entry).content.subheader}}
                                </span>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>

    import mixins from "../mixins"

    export default {
        data () {
            return {
                removing: null
            }
        },
        computed: {
            settings () {
                return this.$store.state.settings
            },
            open () {
                return this.$store.state.runtime.bookmarks.open
            },
            entries () {
                return this.$store.state.runtime.bookmarks.entries
            }
        },
        beforeMount: function() {
            let storage = window.localStorage
            let bookmarks = storage.getItem('rkw-digikit_bookmarks')
            if(bookmarks !== null && typeof JSON.parse(bookmarks) === "object") {
                this.$store.commit('updateBookmarks', bookmarks)
            }
        },
        watch: {
            '$store.state.runtime.bookmarks.entries': function(val, old) {
                let storage = window.localStorage
                storage.setItem('rkw-digikit_bookmarks', JSON.stringify(this.entries))
            }
        },
        mixins: [mixins],
        methods: {
            openContentModal (uid) {
                this.$store.commit('closeBookmarkModal')
                this.$store.commit('setActiveInstance', uid)
                router.push({path: '/instance/' + uid})
            },
            getBookmarkContent (uid) {
                return this.$store.state.data['instances'][uid]
            },
            removeQueue (uid) {
                this.removing = uid
            },
            afterLeave () {
                this.removeBookmark(this.removing)
                this.removing = null
            }
        }
    }
</script>


